@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}
body {
  display: flex;
  flex-direction: row;
  font-family: "Open Sans", sans-serif;
  line-height: 2.5;
}
#navbar {
  position: fixed;
  width: 25%;
  height: 280vh;
  line-height: 1.7;
  // background-color: red;
  header {
    padding: 0 1em;
    font-size: 1.5em;
    font-weight: 700;
  }
  a {
    text-decoration: none;
    color: #1d293a;
    font-size: 1.5rem;
    border-top: 2px solid #1d293a;
    display: block;
    padding: 0 1em;
  }
}
#main-doc {
  width: 80%;
  height: 100vh;
  margin-left: 27em;
  ul > li {
    margin-left: 3em;
  }
}
code {
  background-color: #1d293a;
  color: #ffffff;
  display: inline-block;
  padding: 1em;
  max-width: 35em;
  border-radius: 1em;
}
@media (max-width: 600px) {
  body {
    flex-direction: column;
  }
  #main-doc {
    width: 100%;
    // margin-top: 40em;
    position: absolute;
    top: 42em;
    left: -26em;
    text-align: left;
  }
  #navbar {
    width: 100%;
    position: static;
  }
}
